.CurrencyConverter {
  background-color: rgb(230, 223, 211);
  padding: 10px;
  margin: 5px;
  box-shadow: 0 3px 10px rgb(0, 0, 0, 0.2);
  text-align: center;
  border-radius: 10px;
  max-width: 450px;
}

.CurrencyConverter-button {
  border: 1px solid white;
  padding: 4px 12px;
  border-radius: 5px;
  font-size: large;
  background-color: #00b4d8;
  color: white;
  float: right;
  margin: 15px;
}

.CurrencyConverter-button:hover {
  background-color: coral;
}

.CurrencyConverter-inputBox {
  background-color: rgb(218, 227, 238);
  padding: 30px;
  height: 100px;
  border-radius: 10px;
  box-shadow: 0 3px 10px rgb(0, 0, 0, 0.2);
}

.CurrencyConverter-input,
.CurrencyConverter-options {
  border: none;
  padding: 5px;
  border-radius: 4px;
}
@media (max-width: 550px) {
  .CurrencyConverter {
    max-width: 350px;
  }
  .CurrencyConverter-inputBox {
    padding: 10px;
  }
  .CurrencyConverter-button {
    transform: translateY(-25px);
  }
}
